export class CharactersServices {
  http
  apiConfig

  /**
   *
   * @param config
   * @param http
   */
  constructor (config, http) {
    const checkConfigKeys = Object.keys(config)
    if (!checkConfigKeys.includes('host') ||
        !checkConfigKeys.includes('apiKey') ||
        !checkConfigKeys.includes('version')) {
      throw new Error('params config must have host, version and apiKey information')
    }

    if (typeof http.get !== 'function') {
      throw new Error('params http must have get function that return a Promise')
    }

    this.http = http
    this.apiConfig = {
      host: config.host,
      version: config.version,
      apiKey: config.apiKey
    }
  }

  getCharacterComics (id, params) {
    const httpParams = this.buildParams(Object.assign({orderBy: 'onsaleDate'}, params))

    return this.http
      .get(`${this.apiConfig.host}/${this.apiConfig.version}/public/characters/${parseInt(id, 10)}/comics`, {params: httpParams})
  }

  getCharacter (id, params) {
    const httpParams = this.buildParams(params)

    return this.http
      .get(`${this.apiConfig.host}/${this.apiConfig.version}/public/characters/${parseInt(id, 10)}`, {params: httpParams})
  }

  getCharacters (params) {
    const httpParams = this.buildParams(params)

    return this.http
      .get(`${this.apiConfig.host}/${this.apiConfig.version}/public/characters`, {params: httpParams})
  }

  alterCharacter (character) {
    const favorites = this.getFavorites()

    // add the favorite
    if (favorites.includes(character.id)) {
      character.favorite = true
    }

    return character
  }

  canUseFavorite () {
    return typeof localStorage !== undefined
  }

  getFavorites () {
    if (!this.canUseFavorite()) {
      return []
    }

    let rawFavorites = localStorage.getItem('favorites')

    if (rawFavorites) {
      let favorites = JSON.parse(rawFavorites)
      if (typeof favorites === 'object' && favorites.length !== undefined) {
        return favorites
      }
    }

    return []
  }

  setFavorites (favorites) {
    if (!this.canUseFavorite()) {
      return this
    }

    if (typeof favorites === 'object' && favorites.length !== undefined) {
      localStorage.setItem('favorites', JSON.stringify(favorites))
    }

    return this
  }

  buildParams (params) {
    if (!params) {
      params = {}
    }

    params.apikey = this.apiConfig.apiKey

    return params
  }
}



