<template>
    <div>
        <div style='display:block;height:5px;'>
            <div class='progress' v-if='pending'>
                <div class='indeterminate'></div>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'loader',

    props: {
      'pending': {default: false}
    }
  }
</script>

<style>
</style>



