<template>
    <div>
        <router-link :to="{ name: 'character', params: { id: character.id}}">
            <img v-if="hasImage(character)" v-bind:src="character.thumbnail.path + '.' + character.thumbnail.extension" v-bind:alt="character.name" class="circle">
            <span class="title">{{character.name}}</span>
        </router-link>
    </div>
</template>

<script>
  export default {
    name: 'characterSummary',

    props: [
      'character'
    ],

    methods: {
      hasImage: (character) => {
        return character && character.thumbnail
      }
    }
  }
</script>

<style>
    a {
        cursor: pointer;
    }
</style>



