<template>
    <div>
        <router-link to="/characters"><i class="material-icons">arrow_back</i></router-link>

        <character :character=character :comics=comics :mode='"detail"'></character>

        <loader :pending=pending></loader>
    </div>
</template>

<script>
  import Vue from 'vue'
  import Loader from './Loader.vue'
  import Character from './Character.vue'

  export default {
    name: 'characterPage',

    data: function () {
      return {
        character: {},
        comics: [],
        pending: false
      }
    },
    created: function () {
      // init pendingRequest and its subscription
      Vue.DI.Bus.$on('newRequest', (request) => {
        this.pending = request
      })

      const characterId = parseInt(this.$route.params.id)

      if (isNaN(characterId)) {
        this.$router.go(-1)
      }

      const getCharacters = Vue.DI.CharactersServices.getCharacter(characterId)
        .then(response => {
          try {
            const characters = response.body.data.results

            if (!response.body.data.count) {
              return
            }

            let character = characters[0]

            if (characterId !== character.id) {
              console.info('id are differents', characterId, '!==', character.id)
              return
            }

            Vue.DI.CharactersServices.alterCharacter(character)

            this.character = character
          } catch (e) {
            console.error('unexpected data', e, response)
          }
        }, response => {
          console.warn('http error', response)
        })

      const getCharactersComics = Vue.DI.CharactersServices.getCharacterComics(characterId)
        .then(response => {
          try {
            const comics = response.body.data.results

            if (!response.body.data.count) {
              return
            }

            this.comics = comics
          } catch (e) {
            console.error('unexpected data', e, response)
          }
        }, response => {
          console.warn('http error', response)
        })

      Promise.all([getCharacters, getCharactersComics])
        .then(() => {
          this.pending = undefined
        })
    },

    beforeDestroy: function () {
      // cancel previous request if exists
      if (this.pending) {
        console.info('a request is already running')
        this.pending.abort()
      }
    },

    components: { Character, Loader }
  }
</script>

<style>
</style>



