<template>
    <div>
        <ul class='pagination' v-if='total'>
            <li class='waves-effect'><a v-on:click='gotoFirst'><i class='material-icons'>first_page</i></a></li>
            <li class='waves-effect'><a v-on:click='gotoPrev'><i class='material-icons'>chevron_left</i></a></li>
            <li class='active'><a>{{currentPage}}</a></li>
            <li class='waves-effect'><a v-on:click='gotoNext'><i class='material-icons'>chevron_right</i></a></li>
            <li class='waves-effect'><a v-on:click='gotoLast'><i class='material-icons'>last_page</i></a></li>
        </ul>
    </div>
</template>

<script>

  // @todo use qs to be able to get limit/offset from url (usefull when user does F5, or when we come from external links)
  // 1st: check url to replace default props
  export default {
    name: 'paginator',

    props: {
      'total': {type: Number},
      'limit': {type: Number},
      'offset': {type: Number}
    },
    computed: {
      currentPage: function () {
        return Math.floor(this.offset / this.limit)
      }
    },
    created: function () {
      this.getParamsForFirstPage = () => {
        const offset = 1

        return offset
      }

      this.getParamsForPrevPage = () => {
        const prev = this.offset - this.limit
        const offset = prev >= this.getParamsForFirstPage() ? prev : this.getParamsForFirstPage()

        return offset
      }

      this.getParamsForNextPage = () => {
        const next = this.offset + this.limit
        const offset = next <= this.getParamsForLastPage() ? next : this.getParamsForLastPage()

        return offset
      }

      this.getParamsForLastPage = () => {
        const offset = this.total - this.limit

        return offset
      }
    },
    methods: {
      'gotoFirst': function (ev) {
        ev.preventDefault()

        const params = {offset: this.getParamsForFirstPage()}

        this.$emit('goTo', params)
      },
      'gotoLast': function (ev) {
        ev.preventDefault()
        const params = {offset: this.getParamsForLastPage()}

        this.$emit('goTo', params)
      },
      'gotoPrev': function (ev) {
        ev.preventDefault()
        const params = {offset: this.getParamsForPrevPage()}

        this.$emit('goTo', params)
      },
      'gotoNext': function (ev) {
        ev.preventDefault()
        const params = {offset: this.getParamsForNextPage()}

        this.$emit('goTo', params)
      }
    }
  }
</script>

<style>
    .pagination {
        margin: auto;
        text-align: center;
    }

    a {
        cursor: pointer;
    }
</style>



