<template>
    <div>
        <ul class="collection">
            <li v-for="character in characters" class="collection-item avatar">
                <character :character=character :mode="'summary'"></character>
            </li>
        </ul>
    </div>
</template>

<script>
  import Character from './Character.vue'

  export default {
    name: 'characters',

    props: {
      characters: {type: Array}
    },

    components: { Character }
  }
</script>

<style>
</style>



