<template>
    <div>
        <div class="row">
            <div class="col s12 m6">
                <div v-if="character.id" class="card large">
                    <div class="card-image">
                        <img v-if="hasImage(character)" v-bind:src="character.thumbnail.path + '.' + character.thumbnail.extension" v-bind:alt="character.name">
                        <span class="card-title">{{character.name}}</span>
                    </div>
                    <div class="card-content">
                        {{character.description}}
                        <br />
                        This character appear in {{inComics()}}
                    </div>
                </div>
                <ul v-if="hasComics(character)" class="collection with-header">
                    <li class="collection-header"><h4>First comics</h4></li>
                    <li v-for="comic in comics.slice(0, 3)" class="collection-item">
                        <img v-if="comic.thumbnail" v-bind:src="comic.thumbnail.path + '.' + comic.thumbnail.extension" v-bind:alt="comic.title" class="comics">
                        <caption>{{comic.title}}</caption>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    name: 'characterDetail',

    props: [
      'character',
      'comics'
    ],

    methods: {
      hasImage: function (character) {
        return character && character.thumbnail
      },

      hasComics: function (character) {
        return character && character.comics && character.comics.available
      },

      inComics: function (character) {
        let res = 'no comics.'

        if (this.hasComics(character)) {
          res = character.comics.available + ' comics.'
        }

        return res
      }
    }
  }
</script>

<style>
    .comics {
        width: 50px
    }

    .card.small .card-image, .card.medium .card-image, .card.large .card-image {
        max-width: 50%;
        max-height: 100%;
        margin: auto;
    }

    .card-title {
        width: 100%;
        background: -moz-linear-gradient(top, rgba(0,0,0,0.65) 80%, rgba(0,0,0,0) 100%); /* FF3.6-15 */
        background: -webkit-linear-gradient(top, rgba(0,0,0,0.65) 80%,rgba(0,0,0,0) 100%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to bottom, rgba(0,0,0,0.65) 80%,rgba(0,0,0,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#a6000000', endColorstr='#00000000',GradientType=0 );
    }
</style>



