<template>
    <div>
        <a class="secondary-content"
           v-bind:class="{favorite: character.favorite}"
           v-on:click="toggleFavorite(character, $event)"><i class="material-icons">grade</i></a>
    </div>
</template>

<script>
  import Vue from 'vue'

  export default {
    name: 'characterFavorite',

    props: {
      'character': {type: Object}
    },

    methods: {
      toggleFavorite: function (character, ev) {
        let favorites = Vue.DI.CharactersServices.getFavorites()

        // check character is not already stored: remove it if found
        if (favorites.includes(character.id)) {
          let newFavorites = favorites.filter(id => character.id !== id)
          Vue.DI.CharactersServices.setFavorites(newFavorites)
          // for the reactivity
          character.favorite = false

          return this
        }

        // alert user that he cannot add more favorites
        if (favorites.length >= 5) {
          Vue.DI.Bus.$emit('newError', {'message': 'You already have 5 favorites, you can\'t get more'})

          return this
        }

        // add the new favorites
        favorites.push(character.id)
        Vue.DI.CharactersServices.setFavorites(favorites)

        // for the reactivity
        character.favorite = true

        Vue.DI.Bus.$emit('newSucess', {
          'message': `You added ${character.name} as favorite,
          you now have ${favorites.length} favorites`
        })

        return this
      }
    }
  }
</script>

<style>
    .favorite {
        color: gold
    }

    a {
        cursor: pointer;
    }
</style>



