import { CharactersServices } from './services/characters'
import Toasted from 'vue-toasted'
import PouchDB from 'pouchdb'
import { HttpInterceptors } from './interceptors'
const VueResource = require('vue-resource')

export class VueBuilder {
  build (Vue, config) {
    ['host', 'version', 'apiKey', 'ttl'].forEach(props => {
      if (!config.hasOwnProperty(props)) {
        throw new Error(`Config file mis-configured, missing property ${props}`)
      }
    })

    Vue.config.productionTip = false

    Vue.use(VueResource)
    Vue.use(Toasted, {
      duration: 3000
    })

    Vue.DI = {
      Bus: new Vue(), // is it the right way to add instance ? and to share them ?
      CharactersServices: new CharactersServices(config, Vue.http),
      DB: new PouchDB('characters')
    }

    const interceptors = new HttpInterceptors(Vue, config.ttl)
    interceptors
      .addNewRequestListener(Vue.DI.Bus)
      .addHttpCache(Vue.DI.DB)
  }
}



