<template>
</template>

<script>
  import Vue from 'vue'

  export default {
    name: 'notifications',

    created: function () {
      Vue.DI.Bus.$on('newError', (error) => {
        Vue.toasted.error(error.message)
      })

      Vue.DI.Bus.$on('newSucess', (event) => {
        Vue.toasted.success(event.message)
      })
    }
  }
</script>

<style>
</style>



