<template>
    <div>
        <character-favorite v-if=character :character=character></character-favorite>
        <character-summary v-if="mode === 'summary'" :character=character></character-summary>
        <character-detail v-if="mode === 'detail'" :character=character :comics=comics></character-detail>
    </div>
</template>

<script>
  import CharacterSummary from './CharacterSummary'
  import CharacterDetail from './CharacterDetail'
  import CharacterFavorite from './CharacterFavorite'

  export default {
    name: 'character',

    props: {
      'character': {type: Object},
      'comics': {type: Array},
      'mode': {type: String, default: 'summary'}
    },

    components: {
      CharacterSummary,
      CharacterDetail,
      CharacterFavorite
    }
  }
</script>

<style>
</style>



