<template>
    <div>
        <characters :characters="characters"></characters>

        <loader :pending="pending"></loader>

        <paginator v-bind:total=total v-bind:limit=limit v-bind:offset=offset v-on:goTo="getCharacters"></paginator>
    </div>
</template>

<script>
  import Loader from './Loader.vue'
  import Paginator from './Paginator.vue'
  import Characters from './Characters.vue'
  import Vue from 'vue'

  export default {
    name: 'charactersPage',

    data: function () {
      return {
        characters: [],
        total: 0,
        offset: 100,
        limit: 20,
        pending: false
      }
    },

    created: function () {
      // init pendingRequest and its subscription
      Vue.DI.Bus.$on('newRequest', (request) => {
        this.pending = request
      })

      // get first characters
      this.getCharacters({
        offset: this.offset,
        limit: this.limit
      })
    },

    methods: {
      getCharacters: function (params) {
        // to update the paginator props
        this.offset = params.offset

        // cancel previous request if exists
        if (this.pending) {
          console.info('a request is already running')
          this.pending.abort()
        }

        Vue.DI.CharactersServices.getCharacters(params)
          .then(response => {
            try {
              const characters = response.body.data.results

              if (this.total !== response.body.data.total) {
                this.total = response.body.data.total
              }

              characters.forEach(character => {
                character.favorite = false

                // for the reactivity of the template
                Vue.DI.CharactersServices.alterCharacter(character)
              })

              this.characters = characters
            } catch (e) {
              console.error('unexpected data', e, response)
            }
          }, response => {
            console.warn('http error', response)
          })
          .then(() => {
            this.pending = undefined
          })
      }
    },

    components: { Characters, Loader, Paginator }
  }
</script>

<style>
</style>



